body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-saira: Saira;
  --font-simsun: SimSun;
  --font-trispace: Trispace;
  --font-zen-dots: "Zen Dots";
  --font-poppins: Poppins;

  /* font sizes */
  --font-size-5xl: 24px;
  --font-size-13xl: 32px;
  --font-size-29xl: 48px;
  --font-size-11xl: 30px;

  /* Colors */
  --color-lightsteelblue: #bdd0fb;
  --color-black: #000;
  --color-indianred: rgba(229, 105, 101, 0.78);
  --color-lavender: #e3e8f9;
  --color-gray-100: rgba(0, 0, 0, 0.88);

  /* border radiuses */
  --br-16xl: 35px;
  --br-2516xl: 2535px;
}
